import React, {PropTypes} from 'react';
import { connect } from 'react-redux';
import fetch from 'isomorphic-fetch';


class Post extends React.Component {
  constructor(props){
    super(props);

    this.post = {};
  }
  componentWillMount() {
    this.fetchPost(apiResult => {
      this.renderPost(apiResult)
    });
  }
  render() {
    console.log(this.post);
    return (
      <div class="container blog-detail">
        <div class="wrapper">
          <p class="navigate-link"><a href="/blog"><i class="fa fa-long-arrow-left"></i> back to the blog</a></p>

        </div>
      </div>
    );
  }
  fetchPost(callback) {
    fetch(`http://localhost:3000/api/blog/post/${this.props.params.post}`)
      .then(res => res.json())
      .then(json => callback(json))
      .catch(err => console.log(err));
  }
  renderPost(post) {
    console.log(post);
    return (
      <div class="entry">
        <div class="title-container">
          <h1 class="text-header">Dude</h1>
          <h5 class="meta">Manuel</h5>
        </div>
        <div class="body-container">CONTENT</div>
      </div>
    );
  }
}

Post.propTypes = {
  children: PropTypes.object,
  post: PropTypes.object,
  section: PropTypes.string,
  user: PropTypes.object
}


function mapStateToProps(state) {
  return {
    section: state.site.section,
    post: state.post.post,
    user: state.site.user
  };
}

export default connect(mapStateToProps)(Post);


